html,
body {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
}

.App {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "Georgia", serif;
  transition: background 0.5s ease-in-out;
}

.date {
  position: absolute;
  top: 15px;
  right: 20px;
  font-size: 1rem;
  font-weight: bold;
  color: #fff;
  background: rgba(0, 0, 0, 0.3); /* semi-transparent for readability */
  padding: 5px 10px;
  border-radius: 6px;
}

#quote-box {
  background: linear-gradient(135deg, #ffffff, #f9f9f9);
  padding: 3rem;
  border-radius: 20px;
  width: 500px;
  max-width: 90%;
  box-shadow: 0 12px 30px rgba(0, 0, 0, 0.2);
  text-align: center;
  transition: transform 0.3s ease, box-shadow 0.3s ease, opacity 0.5s ease;
  position: relative;
}

#quote-box:hover {
  transform: scale(1.02);
}

#text {
  font-size: 1.6rem;
  font-weight: 600;
  margin-bottom: 1rem;
  line-height: 1.4;
}

.quote-icon {
  font-size: 6rem;
  vertical-align: top;
  color: #4CAF50;
  margin-right: 8px;
}

#author {
  font-size: 1.1rem;
  margin-bottom: 2rem;
  font-style: italic;
  color: #777;
}

.topic-selector {
  margin-bottom: 20px;
  text-align: center;
}

.topic-selector label {
  font-size: 0.85rem;
  font-weight: 500;
  margin-right: 5px;
  color: #555;
}

.topic-selector select {
  padding: 0.4rem 0.7rem;
  border-radius: 6px;
  border: 1px solid rgba(0,0,0,0.2);
  background: rgba(255,255,255,0.8);
  font-size: 0.85rem;
  font-weight: 500;
  color: #333;
  cursor: pointer;
  appearance: none; /* remove default arrow */
}

.topic-selector select:hover {
  border-color: #aaa;
}

.topic-selector select {
  background: rgba(255,255,255,0.8) url("data:image/svg+xml;utf8,<svg fill='black' height='10' viewBox='0 0 24 24' width='10' xmlns='http://www.w3.org/2000/svg'><path d='M7 10l5 5 5-5z'/></svg>") no-repeat right 10px center;
  background-size: 10px;
  padding-right: 30px;
}

.buttons {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

button,
a {
  padding: 0.7rem 1.2rem;
  border: none;
  border-radius: 6px;
  text-decoration: none;
  cursor: pointer;
  font-size: 0.95rem;
  font-weight: bold;
  transition: all 0.3s ease;
}

button {
  background: #4caf50;
  color: white;
  box-shadow: 0 4px 8px rgba(76, 175, 80, 0.3);
}

button:hover {
  background: #45a049;
  transform: translateY(-2px);
}

a {
  background: #1da1f2;
  color: white;
  box-shadow: 0 4px 8px rgba(29, 161, 242, 0.3);
}

a:hover {
  background: #1991da;
  transform: translateY(-2px);
}

.credits {
  margin-top: 1rem;
  margin-bottom: -4rem;
  font-size: 0.8rem;
  color: #888;
  text-align: center;
  font-style: italic;
}
